.outer-case{
  background:wheat;
  overflow: auto;
}
.inner-case{
  background:#465a65;
  width: 350px;
  height: 410px;
  padding: 20px;
  margin: 100px auto;
  text-align: center;
  border-radius: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
}
.inner-case input[type="text"]{
  width: 92%;
  height: 80px;
  background: transparent;
  padding-left:20px ;
  color: white;
  font-size: 40px;
  margin-bottom: 20px;
  border-radius: 10px;
  border: 2px solid white;
  outline:none;
}
.button-case{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-rows: minmax(60px,auto);
}
.button-case button{
  font-size: 25px;
  margin: 1px;
  outline: none;
  border-radius: 3px;
  box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
}
.button-case button:hover{

}
#clear{
  grid-column: 1/3;
  grid-row: 1;
}
#result{
  grid-column: 3/5;
  grid-row:5;
  background: #f5d108;
}
#clear,#backspace,#diff{
  background: #52c9dc;
}
@media only screen and (max-width: 400px){
  .inner-case{
    width: 300px;
  }
}